$tmo_brand_color: #e20074;
$tmo_bg_color: #e55a72;
.page-template-page-chatbots {
  background-color: #fff;
  line-height: 1.5;
  scroll-behavior: auto;
  overflow-x: hidden;

  & p,
  & ul {
    //font-family: 'Roboto', sans-serif;
  }
}

.page-chatbots__heading {
  justify-content: center;
}

.hero-section-title {
  text-align: left;
  line-height: 1.2em;
  letter-spacing: -1px;
  padding: 0 0 .6em;
  font-size: 44px;

  font-weight: 700;
  font-family: "Roboto", sans-serif;

  @media all and (min-width: 370px) {
    font-size: 48px;
  }
  @media all and (min-width: 841px) {
    font-size: 54px;
  }
  @media all and (min-width: 1200px) {
    //min-width: 730px;
    font-size: 64px;
    line-height: 1.05em;
    letter-spacing: 0px;
    padding-bottom: 0.5em;
  }
  @media all and (min-width: 1440px) {
    font-size: 68px;
  }
  @media all and (min-width: 1600px) {
    font-size: 80px;
  }
  @media all and (max-width: 767px) {
    width: 100%;
    overflow: hidden;
  }
}

.hero-section-subtitle {
  //text-align: left;
  //font-weight: normal;
  //font-style: normal;
  //font-stretch: normal;
  //letter-spacing: normal;
  width: 100%;
  max-width: 300px;
  overflow: hidden;
  line-height: 1.4em;
  color: #666666;
  margin: 0 auto;
  font-size: 16px;
  padding-bottom: 40px;
  @media all and (min-width: 768px) {
    padding-bottom: 48px;
    font-size: 20px;
    margin: 0;
    max-width: 480px;
  }
}

.hero-section-content {
  color: #333;
  background-color: transparent;
  overflow: hidden;
  position: relative;
  width: 100vw;
  max-width: 100vw;
}

.ie11 .hero-comment-wrapper {
  height: 100vh;
  min-height: inherit;
}

.hero-comment-wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  flex-direction: column;
  min-height: 100vh;
  box-sizing: border-box;
  margin: 70px auto;
  @media all and (min-width: 461px) {
    padding: 0;
  }
  @media all and (min-width: 768px) {
    flex-direction: row;
    margin: 0;
    padding: 0;
  }
}

.hero-comment-content {
  width: auto;
  padding: 40px 40px 0;

  //display: flex;
  //flex-direction: column;
  //align-items: center;
  //align-content: center;
  //justify-content: space-between;
  @media all and (min-width: 768px) {
    padding: 0;
  }
}

