.hero-video-wrapper,
.hero-video-start {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.hero-video-image-wrapper {
  margin: auto;
  position: relative;
  padding: 0 0 100%;

  img {
    position: absolute;
    top: 58.5%;
    transform: translate(-50%, -50%);
    left: 59.6%;
    width: auto;
    height: 113%;
  }

  .hero-video-start {
    opacity: 0.8;
    transition: opacity 0.2s ease;
    appearance: none;
    border: 0;
    background-color: rgba(0, 0, 0, 0.53);
    box-shadow: none;
    position: absolute;
    height: 60px;
    width: 60px;
    padding: 0;
    margin: 0;
    border-radius: 50%;
    will-change: opacity;
    display: -webkit-flex;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    justify-content: center;

    &:focus,
    &:hover,
    &:active {
      cursor: pointer;
      outline: none;
      opacity: 1;
    }

    &:before,
    &:after {
      display: block;
      content: '';
      //position: absolute;
      //top: 50%;
      //transform: translateY(-50%);
      //height: 0;
      //width: 1px;
      transition: all 0.1s ease;
    }

    &:before {
      border-top: 13px solid transparent;
      border-bottom: 13px solid transparent;
      border-left: 26px solid white;
      border-right: 0;
      //left: 20px;
      will-change: border-top, border-bottom, border-left;
      margin: 0 0 0 6px;
    }

    &:before {
      //right: 20px;
    }

    &.playing {
      opacity: 0.5;

      &:hover {
        opacity: 1;
      }

      &:before,
      &:after {
        //border-top: 13px solid white;
        //border-bottom: 13px solid white;
        //border-left: 4px solid white;
        border-top: 13px solid white;
        border-bottom: 13px solid white;
        border-left: 5px solid white;
        margin: 0 5px;
      }
    }
  }
}

.hero-video-wrapper {
  width: 38.78%;
  height: 84.3%;
  background-image: url(../images/hero-screen/poster-t-mo.jpg);
  background-size: cover;
  overflow: hidden;

  &.loaded {
    .video-in-frame {
      opacity: 1;
    }
  }

  .video-in-frame {
    opacity: 0;
    transition: opacity 0.8s ease;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 100%;
    width: auto;
  }

  video::-webkit-media-controls-panel {
    display: none !important;
    -webkit-appearance: none;
  }

  /* Old shadow dom for play button */

  video::-webkit-media-controls-play-button {
    display: none !important;
    -webkit-appearance: none;
  }

  /* New shadow dom for play button */

  /* This one works! */

  video::-webkit-media-controls-start-playback-button {
    display: none !important;
    -webkit-appearance: none;
  }
}

.hero-section-content.wide-image-bg.hero-created {
  .hero-cs-comment {
    max-width: 1200px;
    margin: auto;
    padding: 0;
    box-sizing: border-box;
    @media all and (min-width: 768px) {
      padding: 0 30px;
    }
  }
}

// animation for lets chat button
.lets-chat {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1em;
  width: 12.93em;
  height: 3.75em;
  background-color: #f1563c;
  border-radius: 0.625em;
  letter-spacing: 1px;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
  -webkit-transform: scale(0);
  transform: scale(0);
  transition: background-color 0.2s;
}

.lets-chat {
  -webkit-animation-name: growing;
  animation-name: growing;
  -webkit-animation-delay: 0.5s;
  animation-delay: 0.5s;
  -webkit-animation-duration: 0.5s;
  animation-duration: 0.5s;
  -webkit-animation-fill-mode: forwards;
  animation-fill-mode: forwards;
  overflow: hidden;
}

.no-touch .lets-chat:hover {
  background-color: #de3c21;
}

@-webkit-keyframes growing {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

@keyframes growing {
  0% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}

