$size: 166px;
$size_mobile: 166px;
$indent: 15px;
.inner-tile {
  .channel-logo {
    fill: #ccc;
    transition: fill 0.2s ease-out;

    .sms-green,
    .rsc-darkblue,
    .google-grey,
    .google-blue,
    .google-green,
    .google-red,
    .google-yellow {
      fill: #ccc;
      transition: fill 0.2s ease-out;
    }
  }
}

.chatbots-channels-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: -19px auto -7px;
  width: auto;
  max-width: calc(#{$size} * 4 + #{$indent} * 8);

  .column {
    width: $size_mobile;
    height: $size_mobile;
    margin: $indent;
    position: relative;
    box-sizing: border-box;
  }

  .inner-tile {
    position: relative;
    top: 50%;
    left: 50%;
    transition: transform 0.2s cubic-bezier(.69, .12, .3, .92), background-color 0.2s cubic-bezier(.69, .12, .3, .92), z-index 0.2s 0.01s;
    transform: translate(-50%, -50%) scale(0.76);
    transform-origin: center;
    border: 2px solid #d1d1d1;
    border-radius: 18px;
    background-color: #f6f6f6;
    height: calc(1.3 * #{$size_mobile});
    width: calc(1.3 * #{$size_mobile});
    text-align: center;
    padding: 25px;
    box-sizing: border-box;
    overflow: hidden;

    .chatbots-channels-text,
    .chatbots-channels-heading {
      position: absolute;
      top: 80%;
      transform: translateY(-50%);
      left: 0;
      width: 100%;
      padding: 0 20px 30px;
      box-sizing: border-box;
      font-weight: normal;
    }

    .chatbots-channels-text {
      transform: rotate3d(1, 0, 0, 180deg) translateY(-50%);
      transition: transform 0.3s cubic-bezier(.69, .12, .3, .92);
      transform-origin: top;
      font-size: 10px;
      line-height: 1.38;
      color: #aaaaaa;
      backface-visibility: hidden;
    }

    .chatbots-channels-heading {
      opacity: 1;
      font-size: 18px;
      text-align: center;
      color: #666666;
      height: auto;
      transform-origin: top;
      transform: translateY(-50%);
      transition: transform 0.3s cubic-bezier(.69, .12, .3, .92);
      backface-visibility: hidden;
    }
  }

  .inner-link {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
  }

  @media all and (min-width: 360px) {
    .chatbots-channels-text {
      font-size: 14px;
    }
  }
  @media all and (min-width: 600px) {
    margin: 0 auto;
    .column {
      width: $size;
      height: $size;
    }
    .inner-tile {
      height: calc(1.3 * #{$size});
      width: calc(1.3 * #{$size});

      .chatbots-channels-text {
        font-size: 16px;
      }

      .chatbots-channels-heading {
        font-size: 24px;
      }
    }
  }
  @media all and (min-width: 1200px) {
    margin: -15px auto 8px;
  }
  @media all and (min-width: 1600px) {
    margin: -15px auto 0;
  }
}


.channel-icon-wrapper {
  display: block;
