      width: 100%;
      padding: 32px 15px 40px;
      transition: none;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;
      border: none;
      border-radius: 0;
      background-color: transparent;
      text-align: center;
      box-sizing: border-box;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      overflow: unset;

