            font-size: 10px;
        }
      }

      .chatbots-channels-heading {
        opacity: 1;
        letter-spacing: 0.3px;
        font-size: 16px;
        text-align: center;
        color: #666666;
        height: auto;
        margin: 16px auto 10px;
