        .chatbots-channels-text {
          font-size: 9px;
        }
        .chatbots-channels-heading {
          font-size: 14px;
        }
      }
    }

    @for $i from 1 through 9 {
      .mobile-#{$i} {
        order: #{$i};
      }
    }

    .column {
      &:nth-child(2n) .inner-tile:after {
        content: '';
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        height: 60%;
        max-height: 90px;
        width: 1px;
        background-color: #d1d1d1;

        @media all and (min-width: 680px) {
          max-height: 120px;
        }
      }

      .inner-tile:before {
        content: '';
        display: block;
        position: absolute;
        top: 100%;
        left: 20%;
        width: 60%;
        height: 1px;
        background-color: #d1d1d1;
      }

      &.mobile-7 .inner-tile:before,
      &.mobile-8 .inner-tile:before {
        content: none;
      }
    }

    @media all and (max-width: 480px) {
      margin: 0 -15px;
    }

    @media all and (max-width: 390px) {
      margin: 0 -15px;
      .column,
      .inner-tile {
        max-width: 165px;
        width: calc(50vw - 15px);
      }
      .inner-tile {
        padding: 17px;
      }
    }
    @media all and (min-width: 768px) {
      margin: 0 auto;
      max-width: 514px;
      .column {
        width: 260px;
        //height: 240px;
        .inner-tile {
          width: 250px;
          //height: 240px;
          //padding: 30px;
        }
      }
    }
    @media all and (min-width: 1025px) {
      margin: 0 auto;
    }
  }
}

