.usecases-tabs {
  display: block;
  margin: 0;
  min-height: 700px;

  @media all and (min-width: 768px) {
    margin-top: -12px;
  }
  @media (min-width: 768px) and (max-width: 1599px) {
    min-height: 650px;
  }
}

.usecases-heading {
  display: block;
  position: relative;
  min-height: 57px;

  a {
    position: absolute;
    display: block;
    width: auto;
    text-decoration: none;
    text-align: center;
    margin: auto;
    padding: 15px 0;
    box-sizing: border-box;

    &:first-child {
      margin-left: 0;
    }

    .progress,
    .progress-active {
      position: absolute;
      left: 0;
      bottom: 0;
      content: '';
      display: block;
      width: 100%;

    }

    .progress {
      border-bottom: 2px solid #f1563c;
    }

    .progress-active {
      transform: scaleX(0);
      transform-origin: left;
      border-bottom: 2px solid #f1563c;
    }

    h3 {
      font-size: 18px;
      font-weight: bold;
      line-height: 1.5;
      text-align: center;
      color: #aaaaaa;
    }

    &.active {
      .progress {
        border-bottom: 2px solid #aaaaaa;
      }

      .progress-active {
        border-bottom: 2px solid #f1563c;
        transform: scaleX(1);
        transition: transform 5s linear;
      }

      h3 {
        color: #666666;
      }

      & + a + a .progress {
        border-bottom: 2px solid #aaaaaa;
      }

      & + a .progress {
        border-bottom: 2px solid #aaaaaa;
      }
    }

    &.active.clicked .progress-active {
      transition: none;
    }
  }

  @media all and (min-width: 370px) and (max-width: 767px) {
    a {
      h3 {
        font-size: 22px;
      }
    }
  }
  @media all and (max-width: 767px) {
    a {
      width: 100%;
      position: absolute;
      display: block;
      padding: 27px 0;

      h3 {
        opacity: 0;
      }

      z-index: 1;
      margin: 0;

      &.active {
        z-index: 2;

        h3 {
          opacity: 1;
        }
      }

      .progress,
      .progress-active {
        z-index: 1;
        opacity: 1;
        width: 30%;

      }
    }
    a:nth-child(1) .progress,
    a:nth-child(1) .progress-active {
      left: 0;
    }
    a:nth-child(2) .progress,
    a:nth-child(2) .progress-active {
      left: 35%;
    }
    a:nth-child(3) .progress,
    a:nth-child(3) .progress-active {
      left: inherit;
      right: 0;
    }
  }
  @media all and (min-width: 768px) {
    .usecases-tabs {
      min-height: 615px;
    }
    a {
      position: relative;
      display: inline-block;
      width: calc(33.3% - 23px);
      margin: 0 0 0 30px;

      h3 {
        font-size: 16px;
      }
    }
  }
  @media all and (min-width: 1100px) {
    .usecases-tabs {
      min-height: 700px;
    }
    a {
      h3 {
        font-size: 20px;
      }
    }
  }
  @media all and (min-width: 1300px) {
    a {
      h3 {
        font-size: 24px;
      }
    }
  }
}

.usecases-list {
  margin: 40px 0;

  li {
    font-size: 20px;
    font-weight: 300;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.79;
    letter-spacing: normal;
