.usecases-content {
  margin: 1px auto 0;

  .right-part {
    width: 100%;
    margin: 0;
    min-height: 425px;
    position: relative;
    padding: 0;
    box-sizing: border-box;
    background-color: inherit;
  }

  .left-part {
    overflow: hidden;
  }

  .text-tab {
    position: absolute;
    width: 100%;
    left: 50%;
    transform: translateX(-50%);
    top: 34px;
    visibility: hidden;
    opacity: 0;
    z-index: 1;
    transition: opacity 0.3s ease-out, visibility 0.3s 0.01s;

    &.active {
      visibility: visible;
      opacity: 1;
      z-index: 1;
      transition: opacity 0.3s 0.3s ease-out, visibility 0.3s 0.01s;
    }
  }

  .accent-btn {
    color: #f1563c;
    border: solid 1px #f1563c;
    margin-right: -50%;
    transform: translate(-50%, -50%);

    position: absolute;
    bottom: -33%;

    @media all and (min-width: 768px) and (max-width: 1600px)  {
      bottom: -28%;
    }

    &:hover {
      color: white;
      background: #f1563c;
    }
  }

  @media all and (max-width: 767px) {
    .right-part {
      width: auto;
      margin: 0;
      min-height: inherit;
      padding: 0;
      box-sizing: border-box;
      background-color: #fff;
    }
    .text-tab {
      opacity: 1;
      position: static;
      visibility: visible;
      left: inherit;
      transform: none;
      padding: 4px 0;
    }
    .left-part {
      display: none;
    }
    .accent-btn {
      transform: unset;
      position: unset;
      display: block;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 50px;
      width: 170px;
    }
  }
  @media all and (min-width: 768px) {

    height: auto;
    display: flex;
    flex-direction: row;
    .left-part,
    .right-part {
      width: 50%;
      height: calc(50vw - 4px);
      max-height: 450px;
      padding: 30px;
      position: relative;
      box-sizing: border-box;
    }
    .left-part {
      padding: 0;
      background-image: url('../images/services/chatbots/Device.svg');
      background-repeat: no-repeat;
      background-position: 61% 61%;
      background-size: 63% auto;
    }
    .text-tab {
      position: absolute;
      width: 100%;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      visibility: hidden;
      opacity: 0;
      z-index: 1;
      transition: opacity 0.5s ease-out, visibility 0.5s 0.1s;

      &.active {
        visibility: visible;
        opacity: 1;
        z-index: 1;
        transition: opacity 0.6s 0.5s ease-out, visibility 0.5s 0.1s;
      }
    }
  }
}

.inactive .image-tab {
  opacity: 0;
}

.image-tab {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;

  .shadow {
    background-repeat: no-repeat;
    background-position: 90% 25%;
    background-size: 60% auto;
    transition: opacity 0.2s ease-out;

    &.bpa {
      background-image: url(../images/services/chatbots/ba/sbpa.svg);
    }

    &.cc {
      background-position: 91% 25%;
      background-image: url(../images/services/chatbots/cc/scc.svg);
    }

    &.cs {
      background-image: url(../images/services/chatbots/cs/scs.svg);
      background-position: 88% 26%;
    }
  }

  .icon {
    background-repeat: no-repeat;
    background-position: 94% 23%;
    background-size: 60% auto;

    &.bpa {
      background-image: url(../images/services/chatbots/ba/bpa.svg);
    }

    &.cc {
      background-image: url(../images/services/chatbots/cc/cc.svg);
    }

    &.cs {
      background-image: url(../images/services/chatbots/cs/cs.svg);
    }
  }

  .shadow,
  .icon {
    opacity: 0;
    top: 56.5%;
    transform: translateY(-50%);
    height: $tab_height;
    max-height: $max_tab_height;
    width: 79%;
    position: absolute;
  }

  //.icon {
  //  animation: hide 0.6s;
  //}
  z-index: 1;

  &.active {
    .shadow,
    .icon {
      opacity: 1;
    }

    .shadow {
      transition: opacity 0.5s 0.2s linear;
    }

    .icon {
      animation: show 0.6s;
      animation-direction: normal;
    }
  }
}

.image-tab:not(.active) {
  .icon {
    //transform: translateY(-1%);
    //transition: transform 0.5s, opacity 0.5s ease-out, visibility 0.5s 0.1s;
    animation: hide 0.6s;
    animation-direction: normal;
    animation-fill-mode: forwards;
  }

}

