@keyframes show {
  0% {
    top: 30%;
    opacity: 0;
  }
  60% {
    top: 40%;
    opacity: 0;
  }
  75% {
    top: 50%;
    opacity: 0.3;
  }
  100% {
    top: 55%;
    opacity: 1;
  }
}

@keyframes hide {
  0% {
    top: 55%;
    opacity: 1;
  }
  25% {
    top: 58%;
    opacity: 0.3;
  }
  50% {
    top: 65%;
    opacity: 0;
  }
  100% {
    top: 70%;
    opacity: 0;
  }
}

